import React, { useState } from 'react'

const Calculator = () => {
    const [data, setData] = useState("")
    const getValue = (e) => {
        console.log(e.target.value)
        setData(data.concat(e.target.value))
    }

    const calculation = () => {
        setData(eval(data).toString())
    }

    const Back = () => {
        setData(data.slice(0, -1))
    }

    const clear = () => {
        setData("")
    }
    return (
        <>
            <div className="container">
                <div className="row">
                    <div className="col-12"></div>
                </div>
                <div className="row">
                    <div className="col col-12 col-sm-12 ">
                        <div className="">
                            <input type="text" value={data} placeholder='0' onChange={(e) => setData(e.target.value)} className='form-control inp   w-100' />
                        </div>

                        <hr style={{ color: "white" }} />
                        <div className="buttons">
                            <div className="">
                                <button type="button" onClick={getValue} value="(" class="btn btn-light">(</button>
                                <button type="button" onClick={getValue} value=")" class="btn btn-light">)</button>
                                <button type="button" onClick={getValue} value="%" class="btn btn-light">%</button>
                                <button type="button" onClick={clear} class="btn btn-light">AC</button>
                            </div>
                            <div className="">
                                <button type="button" onClick={getValue} value="7" class="btn btn-light">7</button>
                                <button type="button" onClick={getValue} value="8" class="btn btn-light">8</button>
                                <button type="button" onClick={getValue} value="9" class="btn btn-light">9</button>
                                <button type="button" onClick={getValue} value="*" class="btn btn-light">*</button>
                            </div>
                            <div className="">
                                <button type="button" onClick={getValue} value="4" class="btn btn-light">4</button>
                                <button type="button" onClick={getValue} value="5" class="btn btn-light">5</button>
                                <button type="button" onClick={getValue} value="6" class="btn btn-light">6</button>
                                <button type="button" onClick={getValue} value="-" class="btn btn-light">-</button>
                            </div>
                            <div className="">
                                <button type="button" onClick={getValue} value="1" class="btn btn-light">1</button>
                                <button type="button" onClick={getValue} value="2" class="btn btn-light">2</button>
                                <button type="button" onClick={getValue} value="3" class="btn btn-light">3</button>
                                <button type="button" onClick={getValue} value="+" class="btn btn-light">+</button>
                            </div>
                            <div className="">
                                <button type="button" onClick={getValue} value="0" class="btn btn-light">0</button>
                                <button type="button" onClick={Back} class="btn btn-light">Back</button>
                                <button type="button" onClick={calculation} class="btn btn-light">=</button>
                                <button type="button" onClick={getValue} value="/" class="btn btn-light">/</button>
                            </div>
                        </div>


                    </div>

                </div>
            </div>
        </>
    )
}

export default Calculator
